import React from 'react';
import _ from 'lodash';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import Loadable from '@loadable/component';

import PublisherHandler from './PublisherHandler';

import { ResponsiveProps } from '@uz/unitz-components-web/Responsive';

const OTSession = Loadable(() => import('./OpenTok/OTSession'));
const OTPublisher = Loadable(() => import('./OpenTok/OTPublisher'));
const OTStreams = Loadable(() => import('./OpenTok/OTStreams'));
const OTSubscriber = Loadable(() => import('./OpenTok/OTSubscriber'));

function areEqual(prevProps, nextProps) {
  return _.isEqual(prevProps.layout, nextProps.layout);
}

const CallSession = React.memo(({ videoCallModel, layout }) => {
  const isSSR = typeof window === 'undefined';
  if (isSSR) return null;
  return (
    <DIV forceCtx>
      <div onClick={ctx.get('smartGesture.$touchScreen')}>
        <OTSession {...ctx.get('tokbox')}>
          {ctx.get('smartGesture.swap') ? (
            <OTStreams className="w-full h-full">
              <PublisherHandler>
                <OTSubscriber
                  className="w-full h-full bg-transparent"
                  {...ctx.get('tokboxSubscriber')}
                  properties={{
                    showControls: false,
                    height: '100%',
                    width: '100%',
                  }}
                />
              </PublisherHandler>
            </OTStreams>
          ) : (
            <PublisherHandler>
              <OTPublisher
                className="w-full h-full bg-transparent"
                properties={{
                  cameraPosition: videoCallModel.useState('cameraPosition'),
                  publishAudio: !videoCallModel.useState('myState.micMute'),
                  publishVideo: !videoCallModel.useState('myState.videoMute'),
                  width: '100%',
                  height: '100%',
                  showControls: false,
                }}
                eventHandlers={{
                  error: (...args) => {
                    console.log('error', args);
                  },
                  otrnError: (...args) => {
                    console.log('otrnError', args);
                  },
                }}
              />
            </PublisherHandler>
          )}

          <div
            style={{
              ..._.pick(layout, ['width', 'height']),
            }}
          >
            {ctx.get('smartGesture.swap') ? (
              <ResponsiveProps
                sm={{ width: layout.width, height: layout.height / 2 }}
                md={{ width: layout.width / 2, height: layout.height }}
                lg={{ width: layout.width / 2, height: layout.height }}
              >
                {(resProps) => (
                  <div
                    className="absolute bg-transparent"
                    style={{
                      ..._.pick(layout, ['width', 'height']),
                      ...(!ctx.apply('videoCallModel.useState', 'myState.collapse')
                        ? {
                          left: 0,
                          top: 0,
                          width: layout.width / 2,
                          height: layout.height,
                          ...resProps,
                        }
                        : {}),
                    }}
                  >
                    <OTPublisher
                      className="w-full h-full bg-transparent"
                      properties={{
                        cameraPosition: videoCallModel.useState('cameraPosition'),
                        publishAudio: !videoCallModel.useState('myState.micMute'),
                        publishVideo: !videoCallModel.useState('myState.videoMute'),
                        width: '100%',
                        height: '100%',
                        showControls: false,
                      }}
                      eventHandlers={{
                        error: (...args) => {
                          console.log('error', args);
                        },
                        otrnError: (...args) => {
                          console.log('otrnError', args);
                        },
                      }}
                    />
                  </div>
                )}
              </ResponsiveProps>
            ) : (
              <OTStreams>
                <ResponsiveProps
                  sm={{ width: layout.width, height: layout.height / 2 }}
                  md={{ width: layout.width / 2, height: layout.height }}
                  lg={{ width: layout.width / 2, height: layout.height }}
                >
                  {(resProps) => (
                    <div
                      className="absolute bg-transparent"
                      style={{
                        ..._.pick(layout, ['width', 'height']),
                        ...(!ctx.apply('videoCallModel.useState', 'myState.collapse')
                          ? {
                            left: 0,
                            top: 0,
                            width: layout.width / 2,
                            height: layout.height,
                            ...resProps,
                          }
                          : {}),
                      }}
                    >
                      <OTSubscriber
                        className="w-full h-full bg-transparent"
                        {...ctx.get('tokboxSubscriber')}
                        properties={{
                          showControls: false,
                          height: '100%',
                          width: '100%',
                        }}
                      />
                    </div>
                  )}
                </ResponsiveProps>
              </OTStreams>
            )}
          </div>
        </OTSession>
      </div>
    </DIV>
  );
}, areEqual);

const View8 = () => {
  return (
    <DIV className="callSession">
      {ctx.apply('LAYOUT.useLayout', { layout: 'AdvisorVideoCallStatus' }, ({ layout }) => {
        if (!layout) return null;
        if (!ctx.get('tokbox.sessionId')) return null;
        const videoCallModel = ctx.get('videoCallModel');
        return <CallSession videoCallModel={videoCallModel} layout={layout} />;
      })}
    </DIV>
  );
};

export default View8;
