import React from 'react';
import { bindings, hook } from '@vl/redata';
import _ from 'lodash';
import VideoCallModel from '@uz/unitz-models/VideoCallWebModel';
import VideoCallFormatter from '@uz/unitz-models/VideoCallWebModel/formatter';
import PurchaseModel from '@uz/unitz-models/PurchaseModel';
import AdvisorModel from '@uz/unitz-models/AdvisorModel';
import AdvisorProfileFormatter from '@uz/unitz-models/AdvisorProfileModel/formatter';
import CFAdvisorProfileFormatter from '@uz/unitz-models/CFAdvisorProfileModel/formatter';
import usePromiseSource from '@vl/hooks/usePromiseSource';
import { formatVND } from '@vl/mod-utils/currencyFormatter';
import useRoute from '@vl/hooks/useGbRoute';

const CONTROL_BAR_STATUS = ['hide', 'half', 'full'];

const bindData = bindings({
  videoPage: {
    rules: [
      [
        'data',
        {
          data: {
            '@params': hook((ctx) => {
              const route = useRoute();
              const routeParams = _.get(route, 'params', {});
              const advisor_id = _.get(routeParams, 'id');
              const service_id = _.get(routeParams, 'service_id', '');
              const purchase_id = _.get(routeParams, 'purchase_id', '');
              const user_id = ctx.apply('authModel.getUserId');
              const callParams = {
                advisor_id,
                service_id,
                user_id,
                purchase_id,
              };
              return callParams;
            }),

            videoCallModel: hook((ctx) => {
              const advisor_id = ctx.get('@params.advisor_id');
              const videoCallModel = React.useMemo(() => {
                const videoCallModel = VideoCallModel.fromCall(ctx.get('@params'), ctx);
                videoCallModel.createSession(ctx);
                return videoCallModel;
                // eslint-disable-next-line
              }, [advisor_id]);
              videoCallModel.hookState();

              // check call rejected
              videoCallModel.useEffect(
                () => {
                  if (videoCallModel.getState('states.videoCallReject')) {
                    ctx.apply('@page.onCompleted', 'rejected');
                  }
                },
                () => [videoCallModel.getState('states.videoCallReject')]
              );

              // check call no answer
              videoCallModel.useEffect(
                () => {
                  if (videoCallModel.getState('states.isNoAnswer')) {
                    ctx.apply('@page.onCompleted', 'missed');
                  }
                },
                () => [videoCallModel.getState('states.isNoAnswer')]
              );

              // check call finish
              videoCallModel.useEffect(
                () => {
                  if (videoCallModel.hasCallState(VideoCallModel.CALL_STATES.videoCallFinish)) {
                    ctx.apply('@page.onCompleted', 'completed');
                  }
                },
                () => [videoCallModel.getCallState()]
              );

              return videoCallModel;
            }),

            tokbox: hook((ctx) => {
              const voiceCallModel = ctx.get('videoCallModel');
              const tokboxConfig = VideoCallFormatter.getTokboxConfig(ctx)(voiceCallModel);
              return tokboxConfig;
            }),

            tokboxSubscriber: hook((ctx) => {
              const voiceCallModel = ctx.get('videoCallModel');
              const tokboxConfig = VideoCallFormatter.getTokboxSubscriberConfig(ctx)(voiceCallModel);
              return tokboxConfig;
            }),

            '@items': hook((ctx) => {
              const { advisor_id, service_id } = ctx.get('@params');

              const advisor = usePromiseSource(
                async () => {
                  if (!advisor_id) return null;
                  try {
                    const profile = await AdvisorModel.find(
                      `where: {id: {_eq:"${advisor_id}"}}`,
                      `services(where: {id: {_eq: "${service_id}"}}) {
                        price {
                          per_unit
                          per_amount
                          price_currency
                          price_amount
                        
                          
                        }
                        id
                        service {
                          id
                          name
                          
                        }
                      }
                      profile {
                        id
                        display_name
                        avatar_url
                        created_at
                      }
                      reviews {
                        id
                        review_text
                        score
                        updated_at
                        author {
                          profile {
                            avatar_url
                            display_name
                            id
                          }
                        }
                      } review_agg {
                        avg_score
                      } `
                    );
                    return profile.toObject();
                  } catch (err) {}
                },
                null,
                [advisor_id]
              );
              // console.log({ advisor });
              const item = _.get(advisor, '0');
              const service = _.get(advisor, '0.services.0');
              const advisorProfile = _.get(advisor, '0.profile');
              return {
                id: advisor_id,
                service_id,
                advisorProfile: {
                  id: AdvisorProfileFormatter.profileId(ctx)(advisorProfile),
                  displayName: AdvisorProfileFormatter.displayName(ctx)(advisorProfile),
                  avatarUrl: AdvisorProfileFormatter.avatarUrl(ctx)(advisorProfile),
                  rating: {
                    ratingText: AdvisorProfileFormatter.reviewText(ctx)(item),
                    ratingScore: AdvisorProfileFormatter.reviewScore(ctx)(item),
                  },
                  // createdAtTxt: AdvisorProfileFormatter.createdAt(ctx)(advisor),
                },

                price: {
                  pricePerMinTxt: AdvisorProfileFormatter.pricePerMin(ctx)(service),
                  pricePerMinTitle: AdvisorProfileFormatter.pricePerMinTitle(ctx)(service),
                },
              };
            }),
            isCalling: hook((ctx) => {
              return ctx.apply('videoCallModel.hasCallState', VideoCallModel.CALL_STATES.videoCallOnProgress);
            }),
            '@sectionActions': hook((ctx) => {
              const [visibleDropdown, $visibleDropdown] = React.useState(false);
              return {
                visibleDropdown,
                $visibleDropdown: (visible) => {
                  $visibleDropdown(visible);
                },
              };
            }),
            smartGesture: hook((ctx) => {
              const [controlBarStatus, $controlBarStatus] = React.useState('half');
              const [swap, $swap] = React.useState(false);
              const [dragging, $dragging] = React.useState(false);
              return {
                controlBarStatus,
                swap,
                dragging,
                slideUp: () => {},
                $touchScreen: () => {
                  if (!dragging) {
                    const index = _.findIndex(CONTROL_BAR_STATUS, (item) => controlBarStatus === item);
                    if (CONTROL_BAR_STATUS[index + 1]) {
                      $controlBarStatus(CONTROL_BAR_STATUS[index + 1]);
                    } else {
                      $controlBarStatus(CONTROL_BAR_STATUS[0]);
                    }
                  }
                },
                $touchSmallScreen: () => {
                  if (!dragging) {
                    $swap(!swap);
                  }
                },
                onDrag: () => {
                  $dragging(true);
                },
                onStopDrag: () => {
                  setTimeout(() => {
                    $dragging(false);
                  }, 200);
                },
              };
            }),
            '@page': hook((ctx) => {
              const route = useRoute();
              return {
                onCompleted: (status) => {
                  const videoCallModel = ctx.get('videoCallModel');
                  const session_id = videoCallModel.getSectionId();
                  const { purchase_id, advisor_id } = ctx.get('@params');
                  const params = {
                    advisor_id,
                    purchase_id,
                    session_id,
                    step: 'completed',
                    status: status || 'completed',
                  };
                  // Change URL
                  const toUrl = route.redirectUrl(params, { origin: false });
                  route.navigate(toUrl, {
                    relace: true,
                    state: { purchase_id },
                  });
                  // Change Step
                  route.setRouteState(params);
                  ctx.apply('stepModel.setStep', params.step);
                },
              };
            }),
            purchaseData: hook((ctx) => {
              const { purchase_id } = ctx.get('@params');
              const purchaseData = usePromiseSource(
                async () => {
                  if (!purchase_id) return null;
                  try {
                    const purchase = await PurchaseModel.find(
                      `where: {id: {_eq: "${purchase_id}"}}`,
                      `
                      services {
                        id
                        price_amount
                      }
                      discounts {
                        currency
                        percentage
                        amount
                        id
                        type
                      }
                      `
                    );
                    return purchase.toObject();
                  } catch (err) {}
                },
                null,
                [purchase_id]
              );
              const bill = CFAdvisorProfileFormatter.amountPayment(ctx)({
                price: { price_amount: _.get(purchaseData, '0.services.0.price_amount') },
                discounts: _.get(purchaseData, '0.discounts'),
              });
              return {
                totalAfter: _.get(bill, 'totalAfter') ? formatVND(_.get(bill, 'totalAfter')) : 0,
                totalBefore: _.get(bill, 'totalBefore') ? formatVND(_.get(bill, 'totalBefore')) : 0,
                discount: _.get(bill, 'discount') ? formatVND(_.get(bill, 'discount')) : 0,
              };
            }),
          },
        },
      ],
    ],
  },
  sectionVideoActions: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => {
            return (
              true
              || ctx.apply('videoCallModel.hasCallState', VideoCallModel.CALL_STATES.videoCallConnecting)
              || ctx.apply('videoCallModel.hasCallState', VideoCallModel.CALL_STATES.videoCallOnProgress)
            );
          }),
        },
      ],
    ],
  },
  videoCallFinish: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('videoCallModel.hasCallState', VideoCallModel.CALL_STATES.videoCallFinish)),
        },
      ],
      [
        'data',
        {
          data: {},
        },
      ],
    ],
  },
  isLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
  notLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
});

export default bindData;
