import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import Draggable from 'react-draggable'; // Both at the same time
import cx from 'classnames';
import styled from 'styled-components';
import _ from 'lodash';
import { ResponsiveProps } from '@uz/unitz-components-web/Responsive';

const DraggableHandle = styled.div`
  &.fixed-position {
    transform: none !important;
    cursor: default !important;
  }
`;

const PublisherHandler = ({ children, className, ...props }) => {
  return (
    <DIV>
      <ResponsiveProps
        sm={{
          normal: {
            top: 12,
            right: 12,
            width: 120,
            height: 150,
          },
          collapse: {
            top: _.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'height', 0) / 2,
            right: 0,
            width: _.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'width', 0),
            height: _.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'height', 0) / 2,
          },
        }}
        md={{
          normal: {
            top: 46,
            right: 46,
            width: 190,
            height: 106,
          },
          collapse: {
            top: 0,
            right: 0,
            width: _.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'width', 0) / 2,
            height: _.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'height', 0),
          },
        }}
        lg={{
          normal: {
            top: 46,
            right: 46,
            width: 190,
            height: 106,
          },
          collapse: {
            top: 0,
            right: 0,
            width: _.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'width', 0) / 2,
            height: _.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'height', 0),
          },
        }}
      >
        {(resProps) => {
          const width = _.get(resProps, 'width', 190);
          const height = _.get(resProps, 'height', 106);
          const top = _.get(resProps, 'top', 46);
          const right = _.get(resProps, 'right', 46);
          return (
            <div
              onClick={(e) => {
                e.stopPropagation();
                ctx.apply('smartGesture.$touchSmallScreen');
              }}
              style={{
                position: 'absolute',
                top,
                right,
                zIndex: 10,
                width,
                height,
                ..._.get(resProps, 'normal', {}),
                ...(ctx.apply('videoCallModel.useState', 'myState.videoMute')
                  ? {
                    height: 0,
                  }
                  : {}),
                ...(!ctx.apply('videoCallModel.useState', 'myState.collapse')
                  ? {
                    top: 0,
                    right: 0,
                    bottom: 0,
                    width: _.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'width', 0) / 2,
                    height: _.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'height', 0),
                    ..._.get(resProps, 'collapse', {}),
                  }
                  : {}),
              }}
            >
              <Draggable
                axis="both"
                handle=".drag-handle"
                defaultPosition={{ x: 0, y: 0 }}
                position={null}
                grid={[8, 8]}
                scale={1}
                onDrag={ctx.get('smartGesture.onDrag')}
                onStop={ctx.get('smartGesture.onStopDrag')}
                bounds={{
                  top: -top,
                  right,
                  bottom: _.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'height', 0) - top - height,
                  left:
                    0 - (_.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'width', 0) - right - width),
                }}
                disabled={!ctx.apply('videoCallModel.useState', 'myState.collapse')}
              >
                <DraggableHandle
                  className={cx(className, 'w-full h-full drag-handle bg-transparent', {
                    'cursor-move': ctx.apply('videoCallModel.useState', 'myState.collapse'),
                    'fixed-position': !ctx.apply('videoCallModel.useState', 'myState.collapse'),
                  })}
                  {...props}
                >
                  {children}
                </DraggableHandle>
              </Draggable>
            </div>
          );
        }}
      </ResponsiveProps>
    </DIV>
  );
};

export default PublisherHandler;
