import React from 'react';
import { bindings, hook } from '@vl/redata';

import _ from 'lodash';
import useRoute from '@vl/hooks/useGbRoute';

const STEP_LIST = ['payment', 'confirm', 'cancel', 'service', 'completed'];

const STEP_LIST_BY_NAME = {};
_.map(STEP_LIST, (val, index) => (STEP_LIST_BY_NAME[val] = index));

const bindData = bindings({
  serviceSessionPage: {
    rules: [
      [
        'data',
        {
          data: {
            stepModel: hook((ctx) => {
              const route = useRoute();
              const routeParams = _.get(route, 'params', {});
              const step = _.get(routeParams, 'step', _.first(STEP_LIST));

              const [currentStep, $currentStep] = React.useState(step);
              React.useEffect(() => {
                if (currentStep !== step) {
                  $currentStep(step);
                }
              }, [currentStep, $currentStep, step]);

              return {
                currentStep,
                setStep: (newStep) => _.has(STEP_LIST_BY_NAME, newStep) && $currentStep(newStep),
                hasNext: () => {},
                nextStep: () => {},
                prevStep: () => {},
                handlers: {
                  callAgain: () => {
                    const params = {
                      step: 'confirm',
                      status: undefined,
                    };
                    // Change URL
                    const toUrl = route.redirectUrl(params, { origin: false });
                    route.navigate(toUrl, {
                      relace: true,
                    });
                    // Change Step
                    route.setRouteState(params);
                    ctx.apply('stepModel.setStep', params.step);
                  },
                  callFinish: () => {
                    // Change URL
                    const toUrl = ctx.apply('routeStore.toUrl', 'advisor', routeParams);
                    route.navigateExternal(toUrl);
                  },
                },
              };
            }),
          },
        },
      ],
    ],
  },
  paymentStep: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.get('stepModel.currentStep') === STEP_LIST[0]),
        },
      ],
    ],
  },
  confirmStep: {
    rules: [
      [
        'display',
        {
          display: hook(
            (ctx) => _.get(useRoute(), 'params.purchase_id') && ctx.get('stepModel.currentStep') === STEP_LIST[1]
          ),
        },
      ],
    ],
  },
  serviceStep: {
    rules: [
      [
        'display',
        {
          display: hook(
            (ctx) => _.get(useRoute(), 'params.purchase_id') && ctx.get('stepModel.currentStep') === STEP_LIST[3]
          ),
        },
      ],
    ],
  },
  completedStep: {
    rules: [
      [
        'display',
        {
          display: hook(
            (ctx) => _.get(useRoute(), 'params.purchase_id') && ctx.get('stepModel.currentStep') === STEP_LIST[4]
          ),
        },
      ],
    ],
  },
  isLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
  notLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
});

export default bindData;
